  ["14:30 - 15:00", "", "", "Oral \nSession 2", "Oral\nSession\n4", "Oral\nSession\n6", "Closing Remarks, ISMIR 2026", "", ""],
  ["15:00 - 15:30", "", "", "", "", "", "Late-Breaking/Demo", "", ""],
