  ["18:30 - 19:00", "", "Welcome \nReception", "End", "K-Culture Evening", "", "", "", ""],
  ["19:00 - 19:30", "", "", "", "End", "", "", "", ""],
  ["19:30 - 20:00", "", "", "ISMIR\nMusic \nProgram", "Korean \nTraditional \nMusic Concert", "Banquet\n+\nJam session\n", "Rencon (TBD)", "", ""],
  ["20:00 - 20:30", "", "", "End", "End", "", "End", "", ""],
  ["20:30 - 21:00", "", "", "", "", "", "", "", ""],
