    console.log(" :", formatted.rows.length);
    if (formatted.rows.length > 0) {
      console.log("  :", formatted.rows[0]);
      console.log("  :", formatted.rows[1]);
    }
    console.log("===================");
  } catch (err) {
    console.error("  :", err);
    error.value = err.message || "    .";
  } finally {
    loading.value = false;
  }
};

//       
const formatDataAsTable = (data) => {
  if (!data || data.length === 0) {
    return { headers: [], rows: [] };
  }

  //    () 
  const headers = data[0] || [];

  //   4   (0-based index 3)
  const rows = data.slice(4) || [];

  return {
    headers,
    rows
  };
};

//     
onMounted(() => {
  loadSheetData();
